@import url(https://fonts.googleapis.com/css?family=Gelasio:400,600,700&display=swap);
body {
  margin: 0;
  font-family: "Droid", "Gelasio", serif;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  background-color: #eee;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --basket-distance: 20vh;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

/* -------------------------------------------------------------------------- */
/*                               general styling                              */
/* -------------------------------------------------------------------------- */

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

/* -------------------------------------------------------------------------- */
/*                                main styling                                */
/* -------------------------------------------------------------------------- */

main {
  padding-top: 5vh;
  width: calc(100vw - 2 * 20vh);
  width: calc(100vw - 2 * var(--basket-distance));
  max-width: 1000px;
  display: flex;
  flex-direction: column;
}

main > section {
  margin: 2vh 0;
}

/* ------------------------------- decription ------------------------------- */

#description,
img {
  margin: 2vh 20px;
}

#description h1 {
  text-shadow: 2px 1px 2px black;
  font-size: 2.7rem;
  margin-bottom: 2vh;
}

/* ------------------------------- productlist ------------------------------ */

#productList {
  overflow-x: auto;
}

#productList::-webkit-scrollbar {
  cursor: pointer;
}

#productList::-webkit-scrollbar:horizontal {
  height: 15px;
  background-color: rgb(255, 255, 255);
  border-radius: 50px;
  box-shadow: inset 3px -3px 4px rgba(0, 0, 0, 0.1);
}

#productList::-webkit-scrollbar-thumb:horizontal {
  border-radius: 8px;
  border: 2px solid rgb(255, 255, 255);
  background-color: rgba(149, 149, 149, 0.5);
  box-shadow: inset -2px 2px 2px rgba(0, 0, 0, 0.1);
}

/* ---------------------------- price calculation --------------------------- */

#priceCalculation {
  margin: 20px 50px 5vh 0;
}

#priceCalculation > table {
  width: 100%;
  max-width: 950px;
  margin-right: 50px;
  font-size: 1.4rem;
}

#priceCalculation tr:nth-child(-n + 2) {
  color: #6f6f6f;
}

#priceCalculation tr:last-child {
  height: 8vh;
}
#priceCalculation td:first-child {
  padding-left: 20px;
  min-width: 200px;
}
#priceCalculation td:last-child {
  text-align: right;
  padding-right: 20px;
}

/* ------------------------------ submit button ------------------------------ */

#buyButton {
  align-self: flex-end;
  width: 240px;
  margin-right: 70px;
  color: white;
  font-size: 2.7rem;
  padding: 5px 10px 10px 10px;
  background-color: rgb(0, 0, 102);
  border-radius: 15px;
  font-weight: bolder;
  word-spacing: 0px;
  letter-spacing: -2px;
  border: none;
  box-shadow: 4px 4px 6px rgba(0, 0, 0, 0.7);
  outline: none;
  cursor: pointer;
  -webkit-transition-property: background-color;
  transition-property: background-color;
  -webkit-transition-duration: 0.3s;
          transition-duration: 0.3s;
}

#buyButton.deactivated {
  background-color: #666;
  color: rgb(173, 173, 173);
  cursor: auto;
}

#buyButton.activated:hover {
  background-color: #3333ff;
}

/* -------------------------------------------------------------------------- */
/*                                   footer                                   */
/* -------------------------------------------------------------------------- */

footer {
  color: #fff;
  background-color: #666;
  width: 100vw;
  padding: 0 calc(20vh + 20px);
  padding: 0 calc(var(--basket-distance) + 20px);
  border-top: 1px solid rgb(0, 0, 0);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin-top: 5vh;
}

footer > p {
  padding: 20px 0;
}

/* -------------------------------------------------------------------------- */
/*                                    alert                                   */
/* -------------------------------------------------------------------------- */

#alert {
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

#message {
  height: 200px;
  width: 70%;
  max-width: 500px;
  min-width: 342px;
  background: rgb(255, 255, 255);
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    color-stop(59%, rgba(255, 255, 255, 1)),
    to(rgba(220, 220, 220, 1))
  );
  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 1) 59%,
    rgba(220, 220, 220, 1) 100%
  );
  border: 2px solid rgba(0, 0, 102, 1);
  border-radius: 25px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  box-shadow: 2px 2px 10px rgba(192, 192, 192, 0.4);
}

#message span {
  padding: 0 5%;
}

#message span:first-child {
  width: 100%;
  padding: 30px 0;
  border-bottom: 1px solid black;
}

#message span:last-child {
  margin: 20px 50px 0 0;
  align-self: flex-end;
  width: 70px;
  height: 30px;
  background: rgb(255, 255, 255);
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    color-stop(20%, rgb(255, 255, 255)),
    color-stop(65%, rgb(230, 230, 230)),
    to(rgb(163, 163, 163))
  );
  background: linear-gradient(
    180deg,
    rgb(255, 255, 255) 20%,
    rgb(230, 230, 230) 65%,
    rgb(163, 163, 163) 100%
  );
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid rgb(70, 70, 70);
  border-radius: 4px;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.3);
}

#message span:last-child:hover {
  background: rgb(238, 238, 238);
  color: rgb(0, 0, 102);
  border: 2px solid rgb(0, 0, 102);
}

/* -------------------------------------------------------------------------- */
/*                                MEDIA QUERIES                               */
/* -------------------------------------------------------------------------- */

@media (max-width: 440px) {
  #buyButton {
    align-self: center;
    margin-right: 0px;
  }
}

@media (max-width: 520px) {
  #message {
    text-align: center;
  }
}

@media (max-width: 990px) {
  :root {
    --basket-distance: 5vh;
  }
}

@media (min-width: 1300px) {
  footer {
    padding: 0 calc((100vw - 1000px) / 2 + 20px);
  }
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

#itemList {
  width: 100%;
  border-collapse: collapse;
  font-size: 1.4rem;
}

#itemList tr *:first-child {
  padding-left: 20px;
}

#itemList tr > * {
  padding: 10px 0;
}

/* ------------------ seperation table-header vs table-body ----------------- */

#itemList th:nth-child(-n + 4) {
  border-bottom: transparent;
  box-shadow: inset 0px -2px 0px 0px #000;
  line-height: 25px;
}

#itemList tbody tr:first-child {
  border-top: 30px solid transparent;
}

#itemList tbody tr td:nth-child(-n + 4) {
  -webkit-transition-duration: 0.1s;
          transition-duration: 0.1s;
}

#itemList tbody tr:focus-within td:nth-child(-n + 4),
#itemList tbody tr:hover td:nth-child(-n + 4) {
  background-color: #ddd;
}

/* -------------------------- formatting table head sections ------------------------- */

#itemList th:first-child {
  text-align: left;
  padding: 0 20px;
}
#itemList th:nth-child(2) {
  text-align: right;
  padding: 0 20px;
}
#itemList th:nth-child(3) {
  text-align: center;
  padding: 0 20px;
  width: 140px;
}
#itemList th:nth-child(4) {
  text-align: right;
  padding: 0 20px;
}

/* -------------------------- formatting table data sections ------------------------- */

.lineBreak {
  display: none;
}

#itemList td:first-child {
  min-width: 200px;
}

#itemList td span {
  display: inline;
  padding: 0 !important;
}

#itemList td:nth-child(2) {
  text-align: right;
  padding: 0 20px;
}
#itemList td:nth-child(3) {
  text-align: center;
  padding: 0 20px;
}
#itemList td:nth-child(4) {
  text-align: right;
  padding: 0 20px;
  width: 150px;
}

/* ------------------------------- quantifier ------------------------------- */

#quantityBox {
  padding: 0 10px;
  display: grid;
  width: 140px;
  max-width: 140px;
  grid-template-columns: 70% 30%;
  grid-template-rows: 50% 50%;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

input[type="number"] {
  -moz-appearance: textfield;
}

.quantityNumber {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 3;
  font-size: 1.4rem;
  text-align: right;
  padding: 0 10px;
  width: 100%;
  border-radius: 5px;
}

.quantityChanger {
  font-size: 0.7rem;
  margin: 2px 8px;
  background-color: grey;
  color: white;
  cursor: pointer;
  -webkit-transition-duration: 0.2s;
          transition-duration: 0.2s;
  border-radius: 2px;
}
.quantityChanger:hover {
  background-color: rgb(255, 255, 255);
  color: rgb(0, 0, 0);
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}
.quantityChanger:active {
  background-color: rgb(221, 221, 221);
  color: rgb(0, 0, 0);
}

/* ------------------------------ delete ----------------------------- */

#itemList th:last-child {
  width: 50px;
}
#itemList td:last-child {
  width: 50px;
}
#itemList td:last-child div {
  padding: 0 !important;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

#itemList img {
  height: 30px;
  padding: 0 !important;
  margin: 0;
  -webkit-filter: invert(30%) sepia(40%) saturate(4000%) hue-rotate(225deg)
    brightness(55%) contrast(100%);
          filter: invert(30%) sepia(40%) saturate(4000%) hue-rotate(225deg)
    brightness(55%) contrast(100%);
  cursor: pointer;
  -webkit-transition-duration: 0.2s;
          transition-duration: 0.2s;
}
#itemList img:hover {
  -webkit-filter: invert(30%) sepia(50%) saturate(6000%) hue-rotate(225deg)
    brightness(120%) contrast(100%);
          filter: invert(30%) sepia(50%) saturate(6000%) hue-rotate(225deg)
    brightness(120%) contrast(100%);
}

/* -------------------------------------------------------------------------- */
/*                                MEDIA QUERIES                               */
/* -------------------------------------------------------------------------- */

@media (max-width: 850px) {
  .lineBreak {
    display: inline;
    display: initial;
  }
}

@media (min-width: 850px) and (max-width: 990px) {
  .lineBreak {
    display: none;
  }
}

@media (min-width: 990px) and (max-width: 1120px) {
  .lineBreak {
    display: inline;
    display: initial;
  }
}

